*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

body {
  font-family: "Roboto", sans-serif;
  background-color: var(--primary-color);
}

:root {
  /* Light palette */
  --primary-color: #f7f7e6;
  --secondary-color: #0c2231;
  --dark-shadow: rgba(0, 0, 0, 0.5);
  --light-shadow: #163d57;
  --light-text: grey;
  --dark-text: black;
  --border-color: #4e5255;
  --accent-text1: #b6590d;
  --accent-text2: #e06d10;
  --accent-text3: #f4a15d;
  --keys: white;
  --tracker: rgb(233, 255, 206);
  --border-75-transparent: #d6e9f5;

