.text {
  display: flex;
  flex-wrap: wrap;
  padding: 1rem;
  width: 40rem;
}

.text > p {
  font-family: "Special Elite", cursive;
  font-size: 2rem;
  line-height: 1.5;
}

.remaining--letter {
  color: var(--light-text);
}

.previous--letter {
  color: var(--dark-text);
}

.keyboard--key,
.spacebar {
  display: inline-block;
  padding: 10px;
  margin: 2px;
  border-radius: 5px;
  min-width: 40.5px;
  text-align: center;
  font-weight: 500;
  background-color: var(--keys);
  box-shadow: 0px 2.98256px 7.4564px var(--dark-shadow);
}

.active-key__correct {
  border-bottom: green;
  box-shadow: 0 0 1rem green;
  transform: scale(1.1);
}

.active-key__wrong {
  border-bottom: red;
  box-shadow: 0 0 1rem red;
  transform: scale(1.1);
}

.keyboard--top--row {
  position: absolute;
  top: 0;
}
.keyboard--mid--row {
  position: absolute;
  top: 25%;
}
.keyboard--bot--row {
  position: absolute;
  top: 50%;
}

.keyboard--container {
  align-items: center;
  display: flex;
  height: 200px;
  justify-content: center;
  min-width: 70%;
  position: relative;
}

.spacebar--row {
  position: absolute;
  top: 75%;
  width: 15%;
}

.spacebar {
  width: 100%;
}

.tracker--panel--container {
  position: absolute;
  top: 36%;
  transform: translateY(-36%);
  right: 8%;
  height: 32%;
  min-width: 120px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.speed--panel,
.accuracy--panel {
  border-radius: 10px;
  height: 26%;
  padding: 6px;
  width: 100%;
  text-align: center;
  box-shadow: 0px 2.98256px 7.4564px var(--dark-shadow);
  font-weight: 300;
}

.panel--metric {
  margin: 10px;
  display: inline-block;
  font-size: x-large;
  font-weight: 600;
}

.stopwatch {
  position: absolute;
  top: 6%;
  right: 10%;
  font-size: xx-large;
  background-color: var(--primary-color);
  font-weight: 300;
}

.stopwatch--results {
  position: absolute;
  display: flex;
  flex-direction: column;
  left: 3%;
  top: 20%;
  font-size: x-large;
  padding: 20px;
  box-shadow: 0px 2.98256px 7.4564px var(--dark-shadow);
  border: none;
}

.stopwatch--lap {
  padding: 5px;
  font-weight: 300;
}

